.pagination {
  text-align: center;
  margin-top: 40px;
  @include lg {
    margin-top: 30px; }
  @include md {
    margin-top: 15px; }
  li {
    display: inline-block;
    vertical-align: middle;
    margin-left: 2px;
    margin-right: 2px;
    @include md {
      margin-left: 0;
      margin-right: 0; } }
  a {
    @include flex(center, center);
    background-color: $gray-lightest;
    min-width: 32px;
    height: 32px;
    border-radius: 50%;
    color: $gray;
    @include tr(color .3s, background-color .3s, opacity .3s);
    @include md {
      min-width: 25px;
      height: 25px; }
    @include hover {
      background-color: $accent;
      color: $white; }
    &.next, &.prev {
      background-color: transparent;
      color: $black;
      @include before {
        @include icon-arrow(10, 10, 1, currentColor, 45);
        display: block; }
      @include hover {
        background-color: transparent;
        color: $accent; } }
    &.next {
      transform: scaleX(-1); } }
  &__item-current {
    a {
      background-color: $accent;
      color: $white; } } }
