.section {
  padding-top: 50px;
  padding-bottom: 50px;
  @include md {
    padding-top: 30px;
    padding-bottom: 30px; }
  &__title {
    margin-bottom: 50px;
    text-align: center;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 20px; }
    @include sm {
      margin-bottom: 15px; }
    @include xs {
      margin-bottom: 10px; } }

  &--color {
    background-color: #eaeaf6; }
  &--lg {
    padding-top: 115px;
    padding-bottom: 115px;
    @include xl {
      padding-top: 100px;
      padding-bottom: 100px; }
    @include lg {
      padding-top: 80px;
      padding-bottom: 80px; }
    @include md {
      padding-top: 50px;
      padding-bottom: 50px; }
    @include sm {
      padding-top: 30px;
      padding-bottom: 30px; } } }

.section-title {
  @extend .section__title; }

